form {
    width: 90%;
    max-width: 300px;
    background-color: rgba(255, 255, 255, 0.6);
    padding: 33px 40px;
    border-radius: 9px;
    display: grid;
    grid-template-rows: 15% 75% 10%;
    place-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 5;
    -webkit-box-shadow: 0px 0px 66px -4px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 0px 66px -4px rgba(0,0,0,0.75);
box-shadow: 0px 0px 66px -4px rgba(0,0,0,0.75);
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: rgb(224, 105, 224);
    margin-bottom: 26px;
    text-shadow: 1px 0 0 #000, -1px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #dd8944, -1px 1px 0 #dd8944;
}

textarea {
    background-color: white;
    border: solid 2px transparent;
    box-shadow: 0px 5px 50px rgba(54, 176, 197, 0.25);
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 90%;
    width: 100%;
    outline: none;
}

textarea::placeholder {
    color: rgb(8, 235, 235);
    font-size: large;
    font-weight: 400;
}

textarea:focus {
    border:solid 2px rgb(8, 235, 235);
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    font-size: medium;
    width: 120px;
    height: 40px;
    border-radius: 10px;
    border: none;
}

.TodoForm-button--add {
    background: linear-gradient(217deg, rgba(0, 247, 255, 0.8), rgba(255,0,0,0) 70.71%),
            linear-gradient(127deg, rgba(63, 189, 151, 0.8), rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
    color: white;
}

.TodoForm-button--cancel {
    background: linear-gradient(to left, rgb(253, 222, 222), white, rgb(247, 247, 185));
    color: tomato;
}