.counter {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #12c2e9;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #f64f59, #c471ed, #12c2e9);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #e2c7c9, #595bdf, #7acfe2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    font-size: 2.5rem;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.counter-div {
    display: grid;
    place-content: center;
    background: 
        linear-gradient(to top,#312d44,black);
    height: 135px;
    border-bottom: 30px solid rgb(223, 241, 231);
    border-left: none;
    border-right: none;
    z-index: 0
}
.counter-div::before {
    content: '';
    position: absolute;
    width: 100vw;
    height: 65px;
   
    backdrop-filter: blur(12px);
    transform: translateY(15vh);
}

