* {
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
}

.body {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 15% 18% 67%;
    z-index: 0;
    background: #7F7FD5;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #91EAE4, #86A8E7, #7F7FD5);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, rgba(250, 208, 208, 0.432), #c7d7f78e, #b9b6c4a4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
