.ul {
    margin: 10px;
    display: grid;
    grid-template-columns: 
    repeat(auto-fit, minmax(min(350px,100vw),1fr));

    background-image: url('https://img.freepik.com/foto-gratis/escritorio-plano-laico-laptop-gafas_23-2148284275.jpg?w=740&t=st=1679267646~exp=1679268246~hmac=f2bdf16dbd85f14062d0a766ed176a2942ae27f3a5e13f1685b96e992ea3c957');
    object-fit: fill;
}

.todo-list {
    color: white;
    text-shadow: 1px 0 0 #000, -1px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #dd8944, -1px 1px 0 #dd8944;
}