.li-todoLoading{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: larger;
    color: black;
    padding-left: 5%;
    padding-right: 5%;
    box-shadow: 0px 10px 10px -6px black;
}

.li-todoLoading {
    list-style: none;
    text-align: center;
    margin: 20px;
    border-radius: 12px;
    padding: 8px;
    background-color: gray;
    backdrop-filter: blur(5px);
}

.imgLoading-check {
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(5px);
    color: white;
    text-decoration:line-through;
}

.imgLoading-check,
.imgLoading-delete {
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 50%;
    border: solid 2px gray;
}

.text-todoLoading {
    width: 90%;
}  

.imgLoading-delete,
.imgLoading-check,
.text-todoLoading,
.li-todoLoading {
    background: linear-gradient(90deg, rgba(250,
    250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    animation: loadingAnimation 2s ease-in-out infinite;
 }

 @keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
 }