li{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: larger;
    color: black;
    padding-left: 5%;
    padding-right: 5%;
    box-shadow: 0px 10px 10px -6px black;
}

.li {
    list-style: none;
    text-align: center;
    margin: 20px;
    border-radius: 12px;
    padding: 8px;
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(5px);
}

.li-check {
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(5px);
    color: white;
    text-decoration:line-through;
}

.button {
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 50%;
    border: solid 2px black;
}

.button-check {
    background: rgb(131, 202, 131);
}

.button-delete {
    width: 30px;
    height: 30px;
}

.button-delete-completed {
    background-color: tomato;
    border-radius: 8px;
    padding: 2px;
}

.text {
    width: 90%;
    word-wrap: break-word;
}