.todo-seach {
    display: grid;
    grid-template-rows: 30% 70%;
    z-index: 0;
}

.search {
    margin-top: 10%;
    margin-left:12px;
    color: rgb(205, 163, 253);
    font-size: large;
    text-shadow: 1px 0 0 #000, -1px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #dd8944, -1px 1px 0 #dd8944;
}

.lupa {
    width: 20px;
    height: 20px;
}
    
.todoSearch-div {
    display: grid;
    justify-content:flex-start;
}

.input-div {
    background: linear-gradient(to left,rgb(255, 192, 192),gray,blue,red,rgba(236, 89, 229, 0.493));
    border-top-right-radius: 12px;
    border-top-left-radius: 12px;
    height: 30%;
    width: 500px;
    margin: 10px;
}
.input-div::before {
    content: '';
    position:absolute;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    transform: translateY(19px);
    width: 500px;
    height: 25px;
    background: linear-gradient(to left,pink,blue,purple,rgba(240, 3, 240, 0.603));
 }

 .input {
    border: solid 9px transparent;
    position: absolute;
    border-radius: 5px;
    width: 489px;
    margin: 5px;
    outline: none;
}

 @media (max-width: 600px){
      
.input-div {
    width: 300px;
}
.input-div::before {
    width: 300px;
    height: 25px;

 }

 .input {
    width: 288px;
 }
 }