.add{
    position:fixed;
    top: 85vh;
    left: 50vw;
    border: transparent;
    padding: 5px;
    border-radius: 15%; 
    background: #8360c3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #2ebf91, #8360c3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #2ebf9186, #8360c35d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
    z-index: 1;
  }

.add:hover {
    box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 15px;
}
.set-add {
    display: none;
}

.add-img {
    width: 50px;
    height: 50px;
}

.div-add {
    width: 100%;
    display: grid;
    place-content: center;
    
}
.animationOff {
  display: none;
}
.animacion {
    text-align: center;
    font-size: 30px;
    z-index: 1;
    animation: slidein 1s  linear forwards;
}
  
  @keyframes slidein {
    from {
        transform: translateY(0px);
        opacity: 1;
    }
  
    to {
        transform: translateY(-180px);
        opacity: 0;
    }
  }

  .animacionOn{
    display: none;
  }

  .animacion2 {
    text-align: center;
    font-size: 35px;
    z-index: 1;
    animation: slidein2 0.6s  linear forwards;
}
  
  @keyframes slidein2 {
    from {
        transform: translateY(-70px);
        opacity: 1;
    }
  
    to {
        transform: translateY(-150px);
        opacity: 0;
    }
  }

  .animacionOn2{
    display: none;
  }
  .animacion3 {
    text-align: center;
    font-size: 25px;
    z-index: 1;
    animation: slidein3 0.9s  linear forwards;
}
  
  @keyframes slidein3 {
    from {
        transform: translateY(-100px);
        opacity: 1;
    }
  
    to {
        transform: translateY(-200px);
        opacity: 0;
    }
  }

  .animacionOn3{
    display: none;
  }